.root{
    padding: 10px 0px;
    color: white;
    :global{

      .adm-image-viewer-image-wrapper{
        img{
          width: 70%;
        }
      }
        .navbar{
            //border: 2px solid red;
           
        }

        .communityinfo{
            //border: 2px solid red;
            display: flex;
            margin: 10px auto;
            align-items: center;
            .circle1{
                margin: auto 10px auto 20px;
                width: 50px;
                height:50px;
                border-radius: 50%;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;  
                background: linear-gradient(50deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
                .circle2{
                    margin: auto;
                    background: rgba(28, 32, 69, 1);
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;  
                    width: 48px;
                    height: 48px;
                    img{
                        position: relative;
                        margin: auto;
                       
                        width: 45px;
                        height: 45px;
                        border-radius: 50%;
                    }
                }
            }

            .communityname{
                //border: 2px solid red;
                align-items: center;
                vertical-align: bottom;
                height: max-content;
                
                .name1{
                    font-family: Alibaba-Regular;
                    font-size: 16px;

                }
                .name2{
                    font-family: Alibaba-Light;
                    font-size: 10px;
                    letter-spacing: 0.5px;
                }

            }
        }
        .bigtask{
            //border: 2px solid red;
            width: 333px;
            height: 199px;
            border-radius: 20px;
            overflow: hidden;
            background-size:cover;
            background-repeat: no-repeat;
            background-position: center center;
            margin:20px auto;
            overflow: visible;
            color: white;
            .taskname{
              font-family: Alibab-Medium;
              width: 241px;
              height: 37px;
              opacity: 1;
              border-radius: 20px;
              //background: rgba(28, 32, 69, 1);
