              top:150px;
              left: 10px;
              display: flex;
              .name{
                margin: auto;
                font-size: 20px;
                // border:  2px solid red;
                text-align: left;
                text-indent:10px;  
                white-space:nowrap;
                width: 240px;
                height: 35px;
                line-height:35px ;
                opacity: 0.9;
                border-radius: 20px;
                background: rgba(28, 32, 69, 0.5);
                //border: 2px solid red;            
              }
            }
        }


        .title{
            //border: 2px solid rgb(81, 24, 216);
