          }

          .medaldetail{
            //border: 2px solid green;
            font-family: Alibaba-Light;
            text-align: left;
            width: 220px;
            position: relative;
            letter-spacing: 1px;
            font-size: 14px;
            display: flex;
            align-items: center;
          }
        }


        .btnrow{
          //border: 2px solid red;
          display: flex;
          justify-content: center;
          width: 90%;
          margin: auto;
          position: relative;
          .rights{
            //border: 2px solid red;
            width: 208px;
            height: 40px;
            opacity: 1;
            border-radius: 30px;
            background: rgba(42, 110, 229, 1);
            text-align: center;
            line-height: 40px;
            font-size: 14px;
            font-family: Alibaba-Regular;
            letter-spacing: 0.5px;
            position: absolute;
            left:0px;
          }
          .dao{
            
