.popup{
  :global{
    //color: white;
    .adm-popup-body{
      width: 75%;
      height: 280px;
      opacity: 1;
      border-radius: 20px;
      top:220px;
      left: 12.5%;
      //margin: auto;
      display: flex;
      background: linear-gradient(133.65deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
      .card{
        margin: auto;
        width: 98%;
        height: 98%;
        border-radius: 20px;
        background: rgba(28, 32, 69, 1);
        //border: 2px solid red;
        //display: flex;
        padding: 10px 0px;
        .title{
          //border: 2px solid red;
          color: white;
          text-align: center;
          font-size: 16px;
          font-family: Alibaba-Regular;
          margin: 20px auto 30px auto;
        }
        
        input{
          position: relative;
          display: flex;
          width: 200px;
          height: 30px;
          border-radius: 25px;
          background: rgba(255, 255, 255, 1);
          overflow: hidden;
          margin: 20px auto;
          outline-style: none;
          border: 0px;
          padding: 0px 10px;
        }

        .detail{
          //border: 2px solid red;
          color: white;
          font-family: Alibaba-Light;
          font-size: 12px;
          width: 60%;
          margin:auto;
          position: relative;
          
         
        }

        .btn{
          border: none;
          width: 50%;
          height: 32px;
          opacity: 1;
          border-radius: 20px;
          background: rgba(40, 109, 229, 1);
          box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.35);
          text-align: center;
          font-family: Alibaba-Regular;
          color: #fff;
          font-size: 14px;
          margin:auto 25%;
          position: relative;
          top:30px;
          
        }
        .closebtn{
          //border: 2px solid red;
          position: relative;
          top:100px;
          margin: auto;
          text-align: center;
          .icon{
            width: 35px;
            height: 35px;
          }
        }


      }
    }
  }
}



