.root1{
    :global{
        //border: 2px solid red;
        width: 341px;
        height: 141px;
        border-radius: 20px;
        background: rgba(255, 255, 255, 1);
        display: flex;
        margin: 0 auto 20px auto;
        text-decoration-line: none;
        a{
            text-decoration: none;
        }
        
        .image{
            //border: 2px solid red;
            border-radius: 19px;
            width: 178.6px;
            height: 141px;
            overflow: hidden;
            background-size:cover;
            background-repeat: no-repeat;
            background-position: center center;
    
        }

        .nftinfo{
            //border: 2px solid red;
            font-family: Alibaba-Regular;
            position: relative;
            height: 141px;
            left: 10px;
            
            .title{
                //border: 2px solid red;
                margin:10px auto ;
                padding: auto 10px;
                width: 146px;
                height: 24px;
                border-radius: 20px;
                background: rgba(28, 32, 69, 1);
                border: 0.5px solid rgba(0, 0, 0, 1);   
                color: white; 
                font-size: 12px;     
                text-align: left; 
                text-indent:10px;  
                justify-items: center;
                line-height: 24px; 
                white-space:nowrap;
            }
            .intro{
                //border: 2px solid red;
                color: rgba(0, 0, 0, 1);
                font-size: 10px;
                
            }
            .nftnumber{
                //border: 2px solid red;
                font-family: Alibaba-Medium;
                position:absolute;
                bottom: 10px;
                right: 10px;
                color: rgba(0, 0, 0, 1);
                font-size: 12px;
            }
        }

    }
   
    
}




