.root{
    
    :global {
        
        //border: 2px solid rgba(244, 13, 13);
        width : 100%;
        height : 100%;
        position: relative;
        
       
        .bg{
            position:absolute;
            width: 100%;
            height: 100%;
            z-index: 0;
            border-radius: 20px;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.01) 50.62%, rgba(105, 105, 105, 1) 100%);
            box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.25);
            
          }

        .image{
            width: 100%;
            height: 100%;
            background-size:cover;
            background-repeat: no-repeat;
            background-position: center center;
            border-radius: 20px;
            
        }
        .nftinfo{
            //border: 2px solid rgba(244, 13, 13);
            position: absolute;
            bottom: 10px;
            font-family: Alibaba-Regular;
            color:rgba(255, 255, 255, 1);
            font-size: 33px;
            font-weight: 500;
            letter-spacing: 0px;
            max-width: 85%;
            left: 10px;
        
            div{
                
                overflow: hidden;
                white-space:nowrap;/* 不换行 */

            }
           
           
        }
    }   
    
}

  
      
    