}



.thebutton{
    width: auto;
    height: 100%;
    button {
        text-align: center;
        align-items: center;
        background-color: rgba(14, 88, 197, 1);
        padding: 3px 10px;
        border-radius: 8px;
        font-family: Alibaba-Regular;
        font-size: 12px;
        color: rgba(255,255,255,1);
        border:none;
        margin: 0 5px;
    }
}


.mask{
    position: relative;
    top:30vh;
    border-radius: 9px;
    background-color: #fff;
    width: 60%;
    margin: auto;
    align-items: center;
    justify-content: center;

    img{
        border-radius:  9px 9px 0 0;
        position: relative;
        width: 100%;
        margin: 0 auto ;


    }
    div{
        //border: 2px solid red;
        text-align: center;

        font-family: Alibaba-Regular;
        font-size: 15px;
        padding: 20px 0;

    }
}




.popup{
    :global{
      //color: white;
      .adm-popup-body{
        width: 80%;
        height: 400px;
        opacity: 1;
        border-radius: 20px;
        top:150px;
        left: 10%;
        //margin: auto;
        display: flex;
        background: linear-gradient(224.77deg, rgba(255, 255, 255, 1) 0%, rgba(0, 94, 255, 1) 100%);
        
        .card{
          margin: auto;
          width: 99%;
          height: 99%;
          border-radius: 20px;
          background: rgba(28, 32, 69, 1);
          //border: 2px solid red;
          //display: flex;
          padding: 5px 0px;
          .title{
            //border: 2px solid red;
            color: white;
            text-align: center;
            font-size: 14px;
            font-family: Alibaba-Regular;
            margin: 10px auto;
            .icon{
                width: 18px;
                height: 14px;
                margin: auto 5px auto 0px;
              }
          }
          img{
            margin:10px 7.5%;
            width: 85%;
            height: 254px;
            opacity: 1;
            border-radius: 20px;
          }
          .btn{
            border: none;
            width: 170px;
            height: 32px;
            opacity: 1;
            border-radius: 20px;
            background: linear-gradient(90deg, rgba(81, 219, 237, 1) 0%, rgba(111, 127, 242, 1) 49.17%, rgba(197, 110, 253, 1) 100%);
            text-align: center;
            font-family: Alibaba-Regular;
            color: #fff;
            font-size: 14px;
            margin:auto 120px;
            position: relative;
            top:30px;
            
          }
          .closebtn{
            //border: 2px solid red;
            position: relative;
            top:50px;
            margin: auto;
            text-align: center;
            .icon{
              width: 35px;
              height: 35px;
            }
          }
        
  
        }
      }
    }
  }
  
