.root{
   
    :global{
        width: 100%;
        height: 100%;
        position: relative;
        color: white;
        .navbar{
            //border: 2px solid rgb(255, 34, 0);
            position: relative;
            margin:10px auto;
            
        }
        .list{
            //border: 2px solid red;
            width: 92%;
            margin: auto;
            position: relative;
            display: flex;
            .nftitem{
                width: 50%;
                margin: 0px auto 12px auto;
                float: left;
                position: relative;
                //box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
                //border: 2px solid red;
                display: flex;

                .card{
                    background: linear-gradient(135deg, rgba(0, 38, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
                    width: 166px;
                    height: 197px;
                    display:flex;
                    position: relative;
                    margin:auto 2px;
                    text-align: center;
                    justify-content: center;
                    border-radius: 20px;
                    .innercard{
                        display: flex;
                        position: relative;
                        margin: auto;
                        border-radius: 20px;
                        width: 164px;
                        height: 195px;
                        background: rgba(28, 32, 69, 1);
                        .inner{
                            width: 164px;
                            height: 195px;
                            background: rgba(255,255,255,0.1);
                            border-radius: 20px;
                            text-align: center;
                            font-size: 12px;
                            //font-family: Alibaba-Light;
                            //display: flex;
                            .image{
                                margin:5px auto;
                                overflow: hidden;
                                //padding-bottom: 50%;
                                background-size:cover;
                                background-repeat: no-repeat;
                                background-position: center center;
                                border-radius: 6px 6px 0px 0px;
                                width: 155px;
                                height: 155px;
                                opacity: 1;
                                border-radius: 20px;
                            }
                            .image1{
                                margin:5px auto;
                                overflow: hidden;
                                //padding-bottom: 50%;
                                background-size:cover;
                                background-repeat: no-repeat;
                                background-position: center center;
                                border-radius: 6px 6px 0px 0px;
                                width: 155px;
                                height: 155px;
                                opacity: 0.5;
                                border-radius: 20px;
                            }
                            .nftname{
                                text-align: center;
                                align-items: center;
                                //border: 2px solid red;
                                font-family: Alibaba-Regular;
                                font-size: 14px;
                                color:white;
                                position: absolute;
                                bottom: 5px;
                                width: 100%;
                            }
                            .nftname1{
                                text-align: center;
                                align-items: center;
                                //border: 2px solid red;
                                font-family: Alibaba-Regular;
                                font-size: 14px;
                                color:white;
                                position: absolute;
                                bottom: 5px;
                                width: 100%;
                                opacity: 0.5;
                            }
    
                        }
                    }
                }
           
          
                .nftinfo{
                    position: relative;
                    border-radius: 0px 0px 6px 6px;
                    width: 100%;
                    height: 25%;
                    justify-content: center;
                    align-items: center;
                    //border: 3px solid rgb(215, 14, 14);
                    .row1{
                        //border: 2px solid rgb(116, 16, 247);
                        display: flex;
                        padding: 0px 5px 0 7px;
                        height: 48%;
                        align-items: center;
                        .nftname{
                            text-align: left;
                            align-items: center;
                            //border: 2px solid red;
                            font-family: Alibaba-Regular;
                            font-weight: 600;
                            font-size: 12px;
                            width:65% ;
                        
                            color: rgba(31,31,31,1);
                            div{
                                max-width: 80%;
                                overflow: hidden;
                                white-space:nowrap;/* 不换行 */
    
                            }
                        }
                        .nftprice{
                            //border: 2px solid red;
                            width: 35%;
                            font-family: Alibaba-Medium;
                            font-size: 14px;
                            white-space:nowrap;/* 不换行 */
            
    
                        }
    
                    }
                }
              }
        }
      
    }
    
}