

.root{

    :global {
      width: 100%;
      height: 100%;
      //border:3px solid red;
      position: relative;
      padding-bottom: 0px;

      .scrolldiv::-webkit-scrollbar {
        display: none;
      }
    
      .header{
        //border:3px solid red;
        display: flex;
        width: 92%;
        margin: 30px auto 5px auto;
        position: relative;
        .smalltitle{
          //border:2px solid red;
          font-family: Alibaba-Light;
         //font-size: 18px;
          font-size: 11px;
          color: rgba(255, 255, 255, 1);
          text-align: left;
          vertical-align: top;
        }
       
        .search{
          //border:3px solid red;
          position: absolute;
          right: 0;
          font-size: 19px;
         
        }

      }

      .title{
        width: 92%;
        //border:2px solid red;
        font-family: Alibaba-Regular;
       //font-size: 18px;
        font-size: 28px;
        font-weight: 500;
        letter-spacing: -0.05px;
        margin: auto;
        color: rgba(255, 255, 255, 1);
        text-align: left;
        vertical-align: top;
        
        .line{
          width: 61px;
          height: 2px;
          opacity: 1;
          border-radius: 20px;
          background: rgba(14, 88, 197, 1);

        }
      }
      .swiper{
        //border: 2px solid rgb(234, 44, 44);
        height:202px;
        margin: 20px auto;
        border-radius : 20px;
        width : 92%;
     
      
        .swiperitem {
          height: 100%;
          justify-content: center;
          align-items: center;
          user-select: none;
          background-repeat: no-repeat;
          background-size:cover;
          background-position: center center;
        } 
      }



      .hotbanner{
        //border: 2px solid red; 
        width: 92%;
        margin: 10px auto ;
        font-family:Alibaba-Light;
        font-size: 16px;
        font-weight: 600;
        color:rgba(255, 255, 255, 1) ;
        font-size: 16px;
        //font-weight: 700;
      }
      .scrolldiv{
        display: -webkit-box;
        overflow-x: scroll;
        -webkit-overflow-scrolling:touch;
        .scrolldiv::-webkit-scrollbar { width: 0 !important }
        .circle1{
          width: 70px;
          height: 70px;
          border-radius: 50%;
          float:left;
          position: relative;
          margin: auto 0px  auto 14px ;
          display: flex;
          justify-content: center;
          align-items: center;  
          //border: 2px solid red; 
          background: linear-gradient(50deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
 
          .circle2{
            background: rgba(28, 32, 69, 1);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;  
            width: 67px;
            height: 67px;

          }
          
          //border:3px solid red;
         
       
          img{
            width: 59px;
            height: 59px;
            border-radius: 50%;
          }
        }
        
      }
     
    
      .nft-item{
    
        height: 300px;
        width: 339px;
        margin:20px auto 0  auto;
        position: relative;
        //width: 92%;
        //box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
        //border-radius: 20px;
       
      }
    }
  }
  

