.root {
    :global{
        height: 100%;
        width: 100%;
        padding-bottom: 100px;
        overflow-x: hidden;
        color: white;
        .navbar{
            //border: 2px solid red;
            position: relative;
            margin:10px auto 0px auto;
        }

        .nft-image{
            //border: 2px solid rgba(249, 30, 30);
            position: relative;
            justify-content: center;
            align-items: center;
            text-align: center;
            width: 309px;
            display: flex;
            margin: 20px auto;
            box-shadow: 2px 3px 4px 2px rgba(0, 0, 0, 0.25);
            .img{
                margin:auto;
                width: 100%;
                border-radius: 20px;
                pointer-events:none;/* 禁止长按图片保存 */
                -webkit-user-select:none;
                -moz-user-select:none;
                user-select:none;
            }

        }
        .nft-info{
            width: 85%;
            margin:auto;
            //border: 2px solid rgba(249, 30, 30);
            display: flex;
            align-items: center;
            margin:25px auto;
            position: relative;
            .nft-name{
                //border: 2px solid rgba(249, 30, 30);
                font-family: Alibaba-Medium;
                font-size: 16px;
                position: relative;
                left: 0;

            }
            .nft-like{
                //border: 2px solid rgba(249, 30, 30);
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                right: 2px;
                //border: 3px solid rgba(196, 36, 36);
                font-size: 16px;
                .icon{
                    //border: 3px solid rgba(196, 36, 36);
                    height: 15px;
                    width: 15px;
                    margin: auto 8px;
                }

                .likenumber{
                    //border: 3px solid rgba(196, 36, 36);
                    position: relative;
                    font-family: Alibaba-Regular;
                    font-size: 12px;
                }

            }

        }

        .user-info{
            margin: auto;
            width: 92%;
            height: 109px;
            opacity: 1;
            border-radius: 20px;
            background: rgba(9, 16, 87, 1);
            border: 3px solid rgba(255, 255, 255, 0.4);   
            display: flex;  
            .owner{
                //border: 2px solid rgba(249, 30, 30);
                width: 50%;
                height: 100%;
                position: relative;
                .inner{
                    height: 100%;
                    //border: 2px solid rgba(249, 30, 30);
                    display: flex;
                    position: absolute;
                    left: 0px;
                    width: max-content;
                    .userimage{
                        //border: 2px solid rgba(249, 30, 30);
                        width: 50px;
                        height: 50px;
                        opacity: 1;
                        border-radius: 50%;
                        margin: auto 8px;
                        img{
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                            overflow: hidden;
                        }
                    }
                    .userinfo{
                        //border: 2px solid rgba(249, 30, 30);
                        margin: auto ;
                        .remark{
                            //border: 2px solid rgba(249, 30, 30);
                            font-family: Alibaba-Light;
                            font-size: 10px;
                            margin: 3px auto;
    
                        }
                        .username{
                            //border: 2px solid rgba(249, 30, 30);
                            font-family: Alibaba-Regular;
                            font-size: 13px;
                            margin: 3px auto;
                        }
                    }

                }
                
            } 
            .author{
                //border: 2px solid rgba(249, 30, 30);
                width: 50%;
                height: 100%;
                position: relative;
                .inner{
                    height: 100%;
                    //border: 2px solid rgba(249, 30, 30);
                    display: flex;
                    position: absolute;
                    right: 0px;
                    width: max-content;
                
                    .userimage{
                        //border: 2px solid rgba(249, 30, 30);
                        width: 50px;
                        height: 50px;
                        opacity: 1;
                        border-radius: 50%;
                        margin: auto;

                        img{
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                            overflow: hidden;
                        }
                    }
                    .userinfo{
                        //border: 2px solid rgba(249, 30, 30);
                        margin: auto 8px;
                        .remark{
                            //border: 2px solid rgba(249, 30, 30);
                            font-family: Alibaba-Light;
                            font-size: 10px;
                            margin: 3px auto;

                        }
                        .username{
                            //border: 2px solid rgba(249, 30, 30);
                            font-family: Alibaba-Regular;
                            font-size: 13px;
                            margin: 3px auto;
                        }
                    }
                }
            }             
        }


        .brandinfo{
            //border: 2px solid rgba(249, 30, 30);
            width: 92%;
            margin: auto;
            position: relative;
            top:20px;
            .line{
                width: 100%;
                height: 1px;
                background: rgba(204, 204, 204, 0.8);
                margin: 15px auto;
            }
            .title{
                //border: 2px solid rgba(249, 30, 30);
                font-family: Alibaba-Regular;
                font-size: 14px;
                margin: 5px auto;
            }
            .communityname{
                //border: 2px solid rgba(249, 30, 30);
                font-family: Alibaba-Light;
                font-size: 12px;
            }
            .moreinfo{
                //border: 2px solid rgba(249, 30, 30);
                margin: 10px auto;
                position: relative;
                display: flex;
                font-family: Alibaba-Light;
                font-size: 12px;
                .titlename{
                    //border: 2px solid #f91e1e;
                    width: 20%;
                    align-items: center;
                    display: flex;
                }
                .content{
                    width: 80%;
                    //border: 2px solid rgba(249, 30, 30);
                    font-size: 10px;
                    word-break:break-all;
                    text-align: right;
                }
            }
        }
    }
}

.footer{
    :global{
        //border: 2px solid rgba(249, 30, 30);
        position: fixed;
        bottom: 0px;
        height: 80px;
        background-color: rgba(28, 32, 69, 1);
        width: 100%;
        .btn{
            //border: 2px solid rgba(249, 30, 30);
            text-align: right;
            width: 308px;
            height: 50px;
            opacity: 1;
            border-radius: 30px;
            background: linear-gradient(90deg, rgba(81, 219, 237, 1) 0%, rgba(111, 127, 242, 1) 50.27%, rgba(197, 110, 253, 1) 100%);
            display: flex;
            text-align: center;
            justify-content: center;
            align-items: center;
            margin:10px auto;
        
            .price{
                //border: 2px solid rgba(249, 30, 30);
                font-family: Alibaba-Medium;
                font-size: 18px;
    
            }

            
        }
        .btn1{
            //border: 2px solid rgba(249, 30, 30);
            text-align: right;
            width: 308px;
            height: 50px;
            opacity: 1;
            border-radius: 30px;
            background: linear-gradient(90deg, rgba(81, 219, 237, 0.5) 0%, rgba(111, 127, 242, 0.5) 50.27%, rgba(197, 110, 253, 0.5) 100%);
            display: flex;
            text-align: center;
            justify-content: center;
            align-items: center;
            margin:10px auto;
        
            .price{
                //border: 2px solid rgba(249, 30, 30);
                font-family: Alibaba-Medium;
                font-size: 18px;
    
            }

            
        }

     
       
    }
}
  
.popup{

    :global {
        justify-content: center;
        align-items: center;
        .price1{
            margin: 10% auto 2%;
            font-family: Alibaba-Medium;
            font-size: 18px;
            text-align: center;
        }

        .price2{
            margin: 2% auto;
            font-family: Alibaba-Medium;
            font-size: 28px;
            text-align: center;
            
        }
        .price3{
            margin: 2% auto;
            font-family: Alibaba-Regular;
            font-size: 14px;
            text-align: center;
            
        }
        .btn{
            border: none;
            border-radius: 6px;
            background: rgba(14.005811249999988, 87.99140740124997, 196.9875, 1);
            color: rgba(255,255,255,1);
            font-family: Alibaba-Medium;
            font-size: 18px;
            width: 40%;
            height: 40px;
            margin: 2% 30%;
        }
    }
}
    