.root{
    :global{
        //border: 2px solid red;
        
        color: white;
        text-align: center;
        margin: auto;
        border-radius: 10px;
        position: relative;
        a{
          text-decoration: none;
      }
        .title{
            font-family: Alibaba-Regular;
            //border: 2px solid red;
            font-size: 10px;
            width: 90%;
            margin: 15px auto 3px auto;
            text-align: left;
        }

        .scrolldiv::-webkit-scrollbar{display: none;} //隐藏滚动条
        .scrolldiv{
            //border: 2px solid red;
            display: -webkit-box;
            overflow-x: scroll;
            -webkit-overflow-scrolling:touch;
            .circle1{
              width: 59px;
              height: 59px;
              border-radius: 50%;
              float:left;
              position: relative;
              margin: auto 0px  auto 14px ;
              display: flex;
              justify-content: center;
              align-items: center;  
              //border: 2px solid red; 
              background: linear-gradient(50deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
     
              .circle2{
                background: rgba(28, 32, 69, 1);
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;  
                width: 56px;
                height: 56px;
    
              }
             
              img{
                margin: auto;
                width: 50px;
                height: 50px;
                border-radius: 50%;
              }
            }
            
          }


       
          
            .taskscrolldiv::-webkit-scrollbar{display: none;} //隐藏滚动条
            .taskscrolldiv{
              //border: 2px solid red;
              display: -webkit-box;
              overflow-x: scroll;
              -webkit-overflow-scrolling:touch;
              width: 333px;
              margin: auto; 
            
              .smalltask{
                  float: left;
                  //border: 2px solid red;
                  margin: auto 10px auto 0px;
                  width: 150px;
                  height: 60px;
                  opacity: 1;
                  border-radius: 20px;
                  overflow: hidden;
                  background-size:cover;
                  background-repeat: no-repeat;
                  background-position: center center;
                  position: relative;
                  display: flex;

                  .taskname{
                    width: 111px;
                    height: 21px;
                    opacity: 1;
                    border-radius: 20px;
                    //background: rgba(28, 32, 69, 1);
                    background:linear-gradient(90deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
                    position: relative;
                    top:35px;
                    left: 10px;
                    display: flex;
                    .name{
                      margin: auto;
                      font-size: 10px;
                      // border:  2px solid red;
                      text-align: left;
                      text-indent:5px;  
                      white-space:nowrap;
                      width: 110px;
                      height: 20px;
                      line-height:20px ;
                      opacity: 1; 
                      border-radius: 20px;
                      background: rgba(28, 32, 69, 1);
                      color: white;
                      display: flex;
                      vertical-align: middle;
                      align-items: center;
                      line-height: 20px;
                    }
                  }
              }
          }

        

        .bigtask{
            //border: 2px solid red;
            width: 333px;
            height: 199px;
            border-radius: 20px;
            overflow: hidden;
            background-size:cover;
            background-repeat: no-repeat;
            background-position: center center;
            margin:20px auto;
            overflow: visible;
            color: white;
            position: relative;
            .taskname{
              font-family: Alibab-Medium;
              width: 241px;
              height: 37px;
              opacity: 1;
              border-radius: 20px;
              //background: rgba(28, 32, 69, 1);
              background:linear-gradient(90deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
              position: relative;
              top:10px;
              left: 10px;
              display: flex;
              .name{
                margin: auto;
                font-size: 20px;
                // border:  2px solid red;
                text-align: left;
                text-indent:10px;  
                white-space:nowrap;
                width: 240px;
                height: 35px;
                line-height:35px ;
                opacity: 0.9;
                border-radius: 20px;
                background: rgba(28, 32, 69, 1);
                //border: 2px solid red;            
              }
            }
            .infobg{
              //border:  2px solid red;
              width: 100%;
              height: 87px;
              opacity: 1;
              border-radius: 20px;
              background: linear-gradient(180deg,  rgba(28, 32, 69, 0.01) 0%, rgba(28, 32, 69, 1)  100%);
              position: absolute;
              bottom: 0px;
              display: flex;

              .taskinfo{
                //border:  2px solid red;
                position: absolute;
                left: 20px;
                display: flex;
                width: 240px;
                height: 75px;
                margin:15px auto;
                display:table ;      
                
                .row{
                  //border:  2px solid red;
                  display: flex;
                  font-family: Alibaba-Regular;
                  font-size: 14px;
                  margin: 2px auto;
                  
                }
  
              }
  
                .dao{
                  //border: 2px solid red;
                  position: relative;
                  top: 0px;
                  left: 260px;
                  width: 88px;
                  height: 88px;
                  display: flex;
                  border-radius: 50%;
                  background: rgba(28, 32, 69, 1);
                  .circle1{
                    width: 75px;
                    height: 75px;
                    border-radius: 50%;
                    float:left;
                    display: flex;
                    justify-content: center;
                    align-items: center;  
                    margin: auto;
                    //border: 2px solid red; 
                    background: linear-gradient(50deg, rgba(0, 60, 255, 1) 20%, rgba(255, 255, 255, 1) 100%);
                
                    .circle2{
                      background: rgba(28, 32, 69, 1);
                      border-radius: 50%;
                      display: flex;
                      justify-content: center;
                      align-items: center;  
                      width: 72px;
                      height: 72px;
    
                      .discord{
                        width:65px;
                        height: 65px;
                        border-radius: 100px;
                        background:  rgba(40, 109, 229, 1);
                        box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.25);
                        text-align: center;
                        justify-items: center;
                        font-size: 12px;
                        display: flex;
                        div{
                            font-family: Alibaba-Regular;
                            margin: auto;
                            //border: 2px solid red;
                            //
                            .icon{
                              //border: 2px solid red;
                              //color: white;
                              font-size: 16px;
                              margin: 3px auto 0px auto;
  
                            }
                        }
                      }
                    }
                  }
                }

            }

            

        }
 
        
    }
}