.root{
    :global{
        //border: 2px solid red;
        color: white;
        .navbar{
            margin: 10px auto;

        }
        .bgcircle1{
            z-index: 0;
            position: absolute;
            left: 117px;
            top: -104px;
            width: 189px;
            height: 189px;
            border-radius: 50%;
            opacity: 1;
            background: rgba(77, 255, 168, 0.2);
            filter: blur(50px);
        }
        .bgcircle2{
            z-index: 0;
            position: absolute;
            left: 237px;
            top: 16px;
            width: 91px;
            height: 91px;
            border-radius: 50%;
            opacity: 1;
            background: rgba(255, 255, 255, 0.05);
            filter: blur(10px);
        }
        .bgcircle3{
            z-index: 0;
            position: absolute;
            left: -57px;
            top: 91px;
            width: 231px;
            height: 231px;
            border-radius: 50%;
            opacity: 1;
            background: rgba(196, 77, 255, 0.3);
            filter: blur(100px);
        }
        .bgcircle4{
            z-index: 0;
            position: absolute;
            left: 342px;
            top: 107px;
            width: 91px;
            height: 91px;
            border-radius: 50%;
            opacity: 1;
            background: rgba(255, 255, 255, 0.05);
            filter: blur(10px);
        }
        .bgcircle5{
            z-index: 0;
            position: absolute;
            left: 177px;
            top: 248px;
            width: 91px;
            height: 91px;
            border-radius: 50%;
            opacity: 1;
            background: rgba(255, 255, 255, 0.05);
            filter: blur(10px);
        }
        .form{
            //border: 2px solid red;
            background: rgba(28, 32, 69, 1);
            .adm-list-default{
                background: rgba(28, 32, 69, 1);
                border: none;
            }
            .title{
                //border: 2px solid red;
                width: 90%;
                margin: auto;
                font-family: Alibaba-Regular;
                font-size: 22px;
            }
            .notes{
                //border: 2px solid red;
                width: 90%;
                margin: 10px auto;
                font-family: Alibaba-Light;
                font-size: 14px;
              
            }
            .content{
                width: 286px;
                height: 91px;
                margin: 30px auto;
                opacity: 1;
                border-radius: 8px;
                background: rgba(252, 217, 217, 1);
                color: rgba(191, 0, 0, 1);
                font-family: Alibaba-Regular;
                font-size: 14px;
                display: flex;
                padding: 5px 10px;
                align-items: center;
            }
            .box{
                
                width: 286px;
                margin: 20px auto;
                height: 40px;
                opacity: 0.32;
                border-radius: 20px;
                background: rgba(255, 255, 255, 1);
                display: flex;
                color: rgba(163, 163, 163, 1);
                align-items: center;
                padding: 5px;
                font-size: 12px;
                
                input{
                    margin: 0px 5px;
                    outline-style: none;
                    border: 0px;
                    //border-radius: 6px;
                    //border-radius: 20px;
                    //border: 2px solid red;
                    font-size: 12px;
                    color: rgba(163, 163, 163, 1);
                
                  }
                  input::-webkit-input-placeholder{
                  
                    font-family: Alibaba-Regualar;
                    font-size: 12px;
                    //color: rgba(163, 163, 163, 1);
        
                  }
            }

            .submit{
                margin: 40px auto;
                width: 146px;
                height: 44px;
                opacity: 1;
                border-radius: 20px;
                background: rgba(40, 109, 229, 1);
                box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.35);
                font-family: Alibaba-Regualar;
                font-size: 18px;
            }
        }


    }
}