.root{
    //border:3px solid red;
    :global{
        font-family: Alibaba-Medium;
        text-align: center;
        margin: auto;
        border-radius: 10px;
        //margin: 50px;
        .actionimage{
            //border: 2px solid red;
            width: 343px;
            height: 173px;
            border-radius: 20px;
            box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.35);
            overflow: hidden;
            background-size:cover;
            background-repeat: no-repeat;
            background-position: center center;
            margin:0 auto 20px auto;
            color: white;
            .actionname{
                position: relative;
                left: 210px;
                top:20px;
                font-family: Alibab-Medium;
                width: 113px;
                height: 25px;
                opacity: 1;
                border-radius: 20px;
                background: rgba(28, 32, 69, 1);
                background:linear-gradient(90deg, rgba(0, 60, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
                display: flex;
                .name{
                    margin: auto;
                    font-size: 14px;
                    // border:  2px solid red;
                    text-align: center;
                    white-space:nowrap;
                    width: 111px;
                    height: 23px;
                    border-radius: 20px;
                    background: rgba(28, 32, 69, 1);
                    
                }
            }
        }
    }

}
