.root {
    height: 100%;
    width: 100%;
    :global {
      height: 100vh;
      width: 100%;
      // background-image: url('../../assets/Image/bg.png');
      background-size:cover;
      background-repeat: no-repeat;
      background-position: center center;
      display: flex;
      position: relative;
      justify-content: center;
      align-items: center;
      // overflow: hidden;
      // background-size:cover;
      // background-repeat: no-repeat;
      // background-position: center center;
      // position: relative;
      // height: 100vh;
      // width: 100vh;

      
      .bg{
        position:absolute;
        z-index: 0;
        height: 100%; 
        width: 100%;
        background: linear-gradient(180deg, rgba(237, 244, 255, 1) 0%, rgba(207, 204, 204, 0.28) 45.14%, rgba(207, 204, 204, 0) 100%);
        backdrop-filter: blur(15px);
      }

      .loginform{
       
        //-webkit-transform: translateX(-50%) translateY(-70%);
        //border:2px solid red;
        width: 100%;
        //height: 30%;
        z-index: 1;
        position: relative;
        margin: auto;
        display: block;

        .title{
          //border:2px solid red;
          // position: relative;
          justify-content: center;
          align-items: center;
          margin:0px auto 20px auto;
          //border:2px solid red;
          text-align: center;
          font-family: Siyuan-SemiBlod;
          font-size: 40px;
          color: rgba(19, 44, 125, 1);
         
        }
        .slogan{
          font-style: italic;
          //border:2px solid red;
          position: relative;
          text-align: center;
          font-family: Siyuan;
          font-size: 16px;
          color: rgba(19, 44, 125, 1);
          height: max-content;
          width: 80%;
          margin: auto;
        }
        .btn{
          width: 148px;
          height: 45px;
          opacity: 1;
          border-radius: 20px;
          background: rgba(40, 109, 229, 1);
          box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.35);
          color:white;
          font-family: Alibaba-Regular;
          font-size: 18px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 200px auto 0px auto;
          // position: absolute;
          // bottom: 0px;

        }
      }
    }
  }