.root{
    
    justify-content: center; 
    align-items: center;
    
    :global {
       
        width : 100%;
        height : 100%;
        position: relative;
       
       
            .image{
                width: 100%;
                height: 75%;
                overflow: hidden;
                //padding-bottom: 50%;
                background-size:cover;
                background-repeat: no-repeat;
                background-position: center center;
                border-radius: 6px 6px 0px 0px;
            }
      
            .nftinfo{
                position: relative;
                border-radius: 0px 0px 6px 6px;
                width: 100%;
                height: 25%;
                justify-content: center;
                align-items: center;
                //border: 3px solid rgb(215, 14, 14);
                .row1{
                    //border: 2px solid rgb(116, 16, 247);
                    display: flex;
                    padding: 0px 5px 0 7px;
                    height: 48%;
                    align-items: center;
                    .nftname{
                        text-align: left;
                        align-items: center;
                        //border: 2px solid red;
                        font-family: Alibaba-Regular;
                        font-weight: 600;
                        font-size: 12px;
                        width:65% ;
                    
                        color: rgba(31,31,31,1);
                        div{
                            max-width: 80%;
                            overflow: hidden;
                            white-space:nowrap;/* 不换行 */

                        }
                    }
                    .nftprice{
                        //border: 2px solid red;
                        width: 35%;
                        font-family: Alibaba-Medium;
                        font-size: 14px;
                        white-space:nowrap;/* 不换行 */
        

                    }

                }

                .row2{
                    //border: 2px solid rgb(116, 16, 247);
                    padding: 0px 5px;
                    display: flex;
                    color: rgba(128,128,128,1);
                    height: 48%;
                    align-items: center;
                    margin:0 0 2% 0;
                    .userinfo{
                        width: 65%;
                        display: flex;
                        align-items: center;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        //border: 2px solid rgb(0, 76, 255);
                        .avatar{
                    
                            position: relative;
                            //border: 2px solid rgb(175, 242, 19);
                            align-items: center;
                            justify-content: center;
                            justify-items: center;
                            vertical-align:middle;
                        
                            img{
                                width: 18px;
                                height: 18px;
                                border-radius: 50%;
                                margin:0px 2px 2px;
        
                            }
                        }
                        .username{
                            //border: 2px solid red;
                            font-family: Alibaba-Regular;
                            font-size: 11px;
                            align-items: center;
                            color:rgba(128,128,128,1);
                            min-width: 55%;
                            max-width: 75%;
                            div{
                                max-width: 100%;
                                overflow: hidden;
                                white-space:nowrap;/* 不换行 */
        
                            }

                        }
                    }
                    
                    .nftlike{
                        //border: 2px solid red;
                        display: flex;
                        width: 35%;
                        align-items: center;
                        text-align: right;
                        position: relative;
                        left:2px;

                        .icon{
                            width: 10px;
                            height: 10px;
                        }
                        .likenumber{
                            //border: 2px solid red;
                            margin: 0px 0 0 8px ;
                            font-family: Alibaba-Regular;
                            font-size: 11px;
                        
                        }
                    }
                }
            }
        

        
    }   
}

  
      
    