.root {
    height: 100%;
    width: 100%;
    :global {
      height: 100vh;
      width: 100%;
      background-image: url('../../assets/Image/bg.png');
      background-size:cover;
      background-repeat: no-repeat;
      background-position: center center;
      display: table;
      position: relative;
      justify-content: center;
      align-items: center;

      
      .bg{
        position:absolute;
        z-index: 0;
        height: 100%; 
        width: 100%;
        background: linear-gradient(180deg, rgba(237, 244, 255, 1) 0%, rgba(207, 204, 204, 0.28) 45.14%, rgba(207, 204, 204, 0) 100%);
        backdrop-filter: blur(15px);
      }

      .loginform{
        // left: 50%;
        //width:50%;
        //height: 50%;
        //-webkit-transform: translateX(-50%) translateY(-70%);
        //border:2px solid red;
        width: 100%;
        height: 50%;
        z-index: 1;
        justify-content: center;
        align-items: center;
        margin: 35% auto;
        position: absolute;

        .title{
          //border:2px solid red;
          // position: relative;
          justify-content: center;
          align-items: center;
          .name{
            //border:2px solid red;
            text-align: center;
            font-family: Siyuan-SemiBlod;
            font-size: 70px;
            color: rgba(14,88,197,1);
          }
          .slogan{
            font-style: italic;
            //border:2px solid red;
            position: relative;
            top:10%;
            text-align: center;
            font-family: Siyuan;
            font-size: 16px;
            color: rgba(255,255,255,1);
          }
        }

        .input1{
          //border:2px solid red;
          margin: 60px auto 10px auto;
          width: 70%;
          background: #ffff;
          display: flex;
          border-radius: 6px;
          overflow: hidden;
          border-radius: 20px;
          div{
            justify-content: center;
            align-items: center;
            text-align: center;
            line-height: 40px;
            font-family: Alibaba-Regualar;
            font-size: 15px;
            height: 40px;
            color: #000;
            margin: 0px 10px;
          }
          input{
            margin: 0px 5px;
            outline-style: none;
            border: 0px;
            //border-radius: 6px;
         
            width: 70%;
          }
          input::-webkit-input-placeholder{
          
            font-family: Alibaba-Regualar;
            font-size: 12px;

          }
        }


        .input2{
          //border:2px solid red;
          margin: 15px auto;
          width: 70%;
          display: flex;
          overflow: hidden;
          // position: relative;
          justify-content: center;
          align-items: center;
         
          input{
            //border:2px solid red;
            padding: auto auto auto 5px;
            margin: 0px 5px 0px 0px;
            outline-style: none;
            border: 0px;
            border-radius: 20px; 
            height: 40px;
            width: 65%;
            padding: 0 0 0 10px;
          }
          input::-webkit-input-placeholder{
            font-family: Alibaba-Regualar;
            font-size: 12px;
          }

          .button1{
            color: white;
            font-family: Alibaba-Regualar;
            font-size: 14px;
            position: relative;
            min-height: 40px;
            right: 0px;
            height: auto;
            width: 35%;
            border-radius: 6px;
            border: none;
            background: rgba(14,88,197,1);
            border-radius: 20px; 
          }
 
          .button2{
            color: white;
            font-family: Alibaba-Regualar;
            font-size: 13px;
            position: relative; 
            min-height: 40px;
            right: 0px;
            height: auto;
            width: 32%;
            border-radius: 6px;
            border: none;
            background: rgb(229,229,229,1);
            border-radius: 20px; 
          
          }
        }

        .notes{
          //border:2px solid red;
          color: rgba(14, 88, 197, 1);
          text-align: center;
          font-family: Alibaba-Regular;
          font-size: 11px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 30px auto;
          a{
            color: rgba(14, 88, 197, 1);
          }
          .selecticon{
            //border:2px solid red;
            width: 16px;
            height: 16px;
            opacity: 1;
            border-radius: 8px;
            border: 2px solid rgba(255,255,255,1);           
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            vertical-align: middle;
            margin: 0px 5px 0px 0px;
           
          }
          .icon{ 
            // width: 17px;
            // height: 17px;
            font-size: 15px;
            margin: 0px 5px 0px 0px;
          }
          
        }

        .login{
            // position: relative;
            margin: 60px auto 0;
            justify-content: center;
            align-items: center;
            text-align: center;
            button{
              color: white;
              font-family: Alibaba-Medium;
              font-size: 18px;
              height: 40px;
              border-radius: 6px;
              border: none;
              width: 30%;
              background: rgba(14,88,197,1);
              letter-spacing: 3px;
              border-radius: 20px; 
            }
        }
      }
    }
  }